export const getUrl = ({urlFor}) => {
  let url = ""

  if (urlFor === "messages") {
    url = "https://sewavalley.pythonanywhere.com/messenger/"
  } else if (urlFor === "ar") {
    url = "https://vaskrneup.github.io/ARDiseaseVisualizer/"
  } else if (urlFor === "weather") {
    url = "https://welloffexalteddisk.vaskrneup.repl.co/1"
  }

  return url
}