import { Pannellum } from "pannellum-react";
import farmLand360Image from "../../assets/images/360_images/farm_land_360_view.jpg";
import { getCurrentLanguageOutput } from "../../config/languageConfig";
import { Link, useParams } from "react-router-dom";

export const InvestmentDetailView = () => {
  const params = useParams();
  const farmScrip = params["farmCode"];

  return (
    <div>
      <Pannellum
        width="100%"
        height="500px"
        image={farmLand360Image}
        pitch={10}
        yaw={180}
        hfov={110}
        autoLoad
        showZoomCtrl={false}
      />

      <div className={"flex flex-col tracking-normal p-2"}>
        <div className={"flex flex-row justify-between items-center"}>
          <div>
            <h1 className={"text-3xl font-extrabold"}>{farmScrip}</h1>
            <h2 className={"font-thin"}>
              {getCurrentLanguageOutput("Ramechap")},{" "}
              {getCurrentLanguageOutput("Kathmandu")}
            </h2>
            <h2 className={"font-thin"}>
              <span className="font-bold">
                {getCurrentLanguageOutput("Crop")}
              </span>
              : {getCurrentLanguageOutput("Sugarcane")}
            </h2>
            <h2 className={"font-thin"}>
              <span className="font-bold">{getCurrentLanguageOutput("Expected Harvest Date")}</span>:{" "}
              {getCurrentLanguageOutput("May 2022")}
            </h2>
            <h2 className={"font-thin"}>
              <span className="font-bold">{getCurrentLanguageOutput("Expected Profit")}</span>:{" "}
              <span className="text-green-400">{getCurrentLanguageOutput("70%")}</span>
            </h2>
          </div>
          <div>
            <Link to={`/investment-form/${farmScrip}`}>
              <span
                className={
                  "text-sm bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded"
                }
              >
                {getCurrentLanguageOutput("INVEST")}
              </span>
            </Link>
          </div>
        </div>

        <hr className={"opacity-10"} />
        <p className={"text-base mt-2"}>
          {getCurrentLanguageOutput(
            `The farm yields very good profit. Due to its high altitude and location, the field is invulnerable to most natural calamities. The workers of the farm are very hardworking and dedicated.`
          )}
        </p>
      </div>
    </div>
  );
};
