export const getCurrentLanguageOutput = (text) => {
  let langCode = localStorage.getItem("lang")
  if (!langCode) {
    localStorage.setItem("lang", DEFAULT_LANG_CODE)
    langCode = DEFAULT_LANG_CODE
  }

  if (langCode === "en") {
    return text
  }

  if (text in LANGUAGE_CONVERSION) {
    return LANGUAGE_CONVERSION[text][langCode]
  } else {
    return text
  }
}