import {getCurrentLanguageOutput} from "../../config/languageConfig";
import {Link} from "react-router-dom";

export const SharePriceListView = ({scrip, percentageChange, currentPrice, deadline, color = true}) => {
  let indexColorClass = ""
  if (color) {
    indexColorClass = percentageChange > 0 ? "text-green-500" : (percentageChange === 0 ? "text-blue-500" : "text-red-500")
  }

  return (
    <div className="flex shadow-lg flex-row items-center justify-between p-3">
      <div className="font-bold text-2xl">
        <Link to={`/farm-detail/${scrip}`}>{scrip}</Link>
      </div>
      <div className={`flex flex-col font-thin text-sm items-center ${indexColorClass}`}>
        <div className={`tracking-tighter`}>
          {percentageChange}%
        </div>
        <div>
          {currentPrice}
        </div>
      </div>
      <div className="flex flex-col items-center">
        {color ? (<div>
          <Link to={`/investment-form/${scrip}`}>
            <span className="text-sm bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded">
              {getCurrentLanguageOutput("INVEST")}
            </span>
          </Link>
        </div>) : null}
        <div>
          <span className="text-xs">
            {deadline}
          </span>
        </div>
      </div>
    </div>
  )
}

export const ApplyForInvestmentListView = () => {
  return (
    <div>
      <div className="flex flex-col">
        <SharePriceListView
          scrip={getCurrentLanguageOutput("FARM")}
          percentageChange={getCurrentLanguageOutput("change ")}
          currentPrice={getCurrentLanguageOutput("Current Price")}
          deadline={getCurrentLanguageOutput("Deadline")}
          color={false}
        />
        <SharePriceListView
          scrip="FRM1"
          percentageChange={0}
          currentPrice={20}
          deadline={"May 2020, 11"}
        />
        <SharePriceListView
          scrip="FRM2"
          percentageChange={-20}
          currentPrice={20}
          deadline={"May 2020, 11"}
        />
        <SharePriceListView
          scrip="FRM3"
          percentageChange={10}
          currentPrice={20}
          deadline={"May 2020, 11"}
        />
      </div>
    </div>
  )
}