import {getCurrentLanguageOutput} from "../../config/languageConfig";
import {useEffect, useState} from "react";
import axios from "axios";
import {getUrl} from "../../config/urlConfig";

export const ExpertFeedback = () => {
  let [message, setMessage] = useState([])
  const [typedMsg, setTypedMsg] = useState({"msg": ""})

  useEffect(() => {
    setInterval(() => {
      axios.get(getUrl({urlFor: "messages"})).then(resp => {
        if (resp.data["messages"]) {
          setMessage(resp.data["messages"])
        }
      })
    }, 2000)
  }, [])

  if (!message) {
    message = []
  }

  const username = "Sitaram Pandey"

  return (
    <div className="flex flex-col">
      <div className="mt-5 mb-16">
        <div id="messages">
          {message.map((data) => {
            return (
              <>
                {
                  data["sender"] === username ? (
                    <div className="clearfix" key={data["pk"]}>
                      <div className="bg-green-500 float-right w-3/4 mx-4 my-2 p-2 rounded-lg clearfix">
                        {data["message_text"]}
                      </div>
                    </div>
                  ) : (
                    <div className="clearfix" key={data["pk"]}>
                      <div className="bg-gray-500 w-3/4 mx-4 my-2 p-2 rounded-lg">
                        {data["message_text"]}
                      </div>
                    </div>
                  )
                }
              </>
            )
          })}
        </div>
      </div>
      <div className="">
        <form className="fixed w-full flex justify-between bg-green-100 bottom-0" onSubmit={(e) => {
          e.preventDefault()
        }}>
          <input
            className="flex-grow m-2 py-2 px-4 mr-1 rounded-full border border-gray-300 bg-gray-200 resize-none text-black"
            placeholder={`${getCurrentLanguageOutput("Message")}...`}
            value={typedMsg["msg"]}
            onChange={(e) => {
              setTypedMsg({"msg": e.target.value})
            }}
          />
          <button
            className="m-2"
            type={"submit"}
            onClick={(e) => {
              e.preventDefault()
              setTypedMsg({"msg": ""})
              axios.post(getUrl({urlFor: "messages"}), {
                "sender": username,
                "sent_date": "2020/11/21 10:23:11",
                "message_text": typedMsg["msg"]
              }).then()
            }}
          >
            <svg
              className="svg-inline--fa text-green-500 fa-paper-plane fa-w-16 w-12 h-12 py-2 mr-2"
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="paper-plane"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 512 512"
            >
              <path
                fill="currentColor"
                d="M476 3.2L12.5 270.6c-18.1 10.4-15.8 35.6 2.2 43.2L121 358.4l287.3-253.2c5.5-4.9 13.3 2.6 8.6 8.3L176 407v80.5c0 23.6 28.5 32.9 42.5 15.8L282 426l124.6 52.2c14.2 6 30.4-2.9 33-18.2l72-432C515 7.8 493.3-6.8 476 3.2z"
              />
            </svg>
          </button>
        </form>
      </div>
    </div>
  );
};
