import {useState} from "react";
import {getCurrentLanguageOutput} from "../../config/languageConfig";
import {useParams} from "react-router-dom";

export const InvestmentForm = () => {
  const [percentageHolding, setPercentageHolding] = useState(0)
  const [investmentAmt, setInvestmentAmt] = useState(0)
  const params = useParams();
  const farmScrip = params["farmCode"]
  const total_amount = 100000

  return (
    <div>
      <form
        className="bg-indigo-900 bg-opacity-5 shadow-md rounded px-8 pt-6 pb-8 mb-4 flex flex-col py-4">
        <div className="mb-4">
          <label className="block text-white-700 text-sm font-bold mb-2" htmlFor="name">
            {getCurrentLanguageOutput("Full Name")}
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
            id="name" type="text" placeholder={getCurrentLanguageOutput("Full Name")} disabled={true}
            value={"John Wick"}/>
        </div>
        <div className="mb-4">
          <label className="block text-white-700 text-sm font-bold mb-2" htmlFor="email">
            {getCurrentLanguageOutput("Email")}
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
            id="email" type="email" placeholder={getCurrentLanguageOutput("Email")} disabled={true}
            value={"johnwick@example.com"}/>
        </div>
        <div className="mb-4">
          <label className="block text-white-700 text-sm font-bold mb-2" htmlFor="phone">
            {getCurrentLanguageOutput("Phone")}
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
            id="phone" type="text" placeholder={getCurrentLanguageOutput("Phone")} disabled={true}
            value={"9800000000"}/>
        </div>
        <div className="mb-4">
          <label className="block text-white-700 text-sm font-bold mb-2" htmlFor="scrip">
            {getCurrentLanguageOutput("SCRIP")}
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
            id="scrip" type="text" placeholder={getCurrentLanguageOutput("Phone")} disabled={true}
            value={farmScrip}
          />
        </div>
        <div className="mb-4">
          <label className="block text-white-700 text-sm font-bold mb-2" htmlFor="donation_amount">
            {getCurrentLanguageOutput("Investment Amount")}
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="donation_amount" type="number" placeholder={getCurrentLanguageOutput("Investment Amount")} min={100}
            max={total_amount}
            value={investmentAmt}
            onChange={(e) => {
              setInvestmentAmt(prevState => {
                if (e.target.value < 0) {
                  return prevState
                }
                if (e.target.value <= total_amount) {
                  return e.target.value
                } else {
                  return prevState
                }
              })
              setPercentageHolding((prevState => {
                if (e.target.value <= total_amount) {
                  return parseFloat(`${(e.target.value / total_amount) * 90}`).toFixed(2)
                } else {
                  return prevState
                }
              }))
            }}
          />
          <p className="font-thin text-xs">
            {getCurrentLanguageOutput("Percentage Holdings on Farm")}:
            <span className={"font-bold"}> {percentageHolding}% </span>
            {getCurrentLanguageOutput("out of")}
            <span className={"font-bold"}> {total_amount}</span>
          </p>
        </div>

        <div className="items-center justify-between flex flex-col">
          <button
            className="bg-red-500 w-full mb-2 hover:bg-red-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="submit">
            {getCurrentLanguageOutput("PAY WITH CONNECTIPS")}
          </button>
          {
            investmentAmt > 10000 ? null : (
              <button
                className="bg-green-500 w-full hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                type="submit">
                {getCurrentLanguageOutput("PAY WITH ESEWA")}
              </button>
            )
          }
        </div>
      </form>
    </div>
  )
}