import {getCurrentLanguageOutput} from "../../config/languageConfig";
import {useState} from "react";

export const AddStock = ({setOpen}) => {
  const [productData, setProductData] = useState({
    productName: "",
    productQuantity: 0,
    costPrice: 0,
    sellPrice: 0,
    profit: 0,
  });

  const handleProductData = (e) => {
    setProductData((prevState) => {
      let profit = prevState.profit;
      if (e.target.name === "costPrice") {
        profit = prevState.sellPrice - e.target.value;
      } else if (e.target.name === "sellPrice") {
        profit = e.target.value - prevState.costPrice;
      }

      return {
        ...prevState,
        [e.target.name]: e.target.value,
        profit: profit * prevState.productQuantity,
      };
    });
  };

  return (
    <div className={"flex flex-col"}>
      <div>
        <form className="bg-indigo-900 bg-opacity-5 shadow-md rounded px-8 pt-6 pb-8 mb-4 flex flex-col py-4">
          <div className="mb-4">
            <label
              className="block text-white-700 text-sm font-bold mb-2"
              htmlFor="productName"
            >
              {getCurrentLanguageOutput("Product Name")}
            </label>
            <input
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
              id="productName"
              type="text"
              name={"productName"}
              placeholder={getCurrentLanguageOutput("Product Name")}
              onChange={handleProductData}
              value={productData["productName"]}
            />
          </div>
          <div className={"flex mb-4"}>
            <div className="mr-1">
              <label
                className="block text-white-700 text-sm font-bold mb-2"
                htmlFor="productQuantity"
              >
                {getCurrentLanguageOutput("Product Quantity")}
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
                id="productQuantity"
                type="text"
                name={"productQuantity"}
                placeholder={getCurrentLanguageOutput("Product Quantity")}
                onChange={handleProductData}
                value={productData["productQuantity"]}
              />
            </div>
            <div className={"ml-1"}>
              <label
                className="block text-white-700 text-sm font-bold mb-2"
                htmlFor="costPrice"
              >
                {getCurrentLanguageOutput("Cost Price")}
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
                id="costPrice"
                type="text"
                name={"costPrice"}
                placeholder={getCurrentLanguageOutput("Cost Price")}
                onChange={handleProductData}
                value={productData["costPrice"]}
              />
            </div>
          </div>
          <div className={"flex mb-4"}>
            <div className="mr-1">
              <label
                className="block text-white-700 text-sm font-bold mb-2"
                htmlFor="sellPrice"
              >
                {getCurrentLanguageOutput("Sell Price")}
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
                id="sellPrice"
                type="text"
                name={"sellPrice"}
                placeholder={getCurrentLanguageOutput("Sell Price")}
                onChange={handleProductData}
                value={productData["sellPrice"]}
              />
            </div>
            <div className={"ml-1"}>
              <label
                className="block text-white-700 text-sm font-bold mb-2"
                htmlFor="profit"
              >
                {getCurrentLanguageOutput("Profit")}
              </label>
              <input
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline disabled:opacity-50"
                id="profit"
                type="text"
                name={"profit"}
                disabled={true}
                placeholder={getCurrentLanguageOutput("Profit")}
                onChange={handleProductData}
                value={productData["profit"]}
              />
            </div>
          </div>
          <div className="flex">
            <button
              className="bg-green-500 w-full mr-2 hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              type="submit"
            >
              {getCurrentLanguageOutput("SAVE")}
            </button>
            <button
              className="bg-red-500 w-full ml-2 hover:bg-red-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              type="button"
              onClick={() => {
                setOpen("invisible")
              }}
            >
              {getCurrentLanguageOutput("CLOSE")}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};


export const PopupView = (props) => {
  return (
    <div className="absolute top-0 w-screen h-screen  bg-opacity-0">
      <div className="flex h-full w-full flex-col justify-center backdrop-filter backdrop-blur-lg" {...props}>

      </div>
    </div>
  )
}

export const StockManagement = () => {
  const [open, setOpen] = useState("invisible")

  return (
    <div>
      <div className="mt-4">
        <div className="flex shadow-lg flex-row items-center justify-between p-3">
          <div className="font-bold text-2xl">{getCurrentLanguageOutput("Product")}</div>
          <div className="flex flex-col font-thin text-sm items-center">
            <div className="tracking-tighter">{getCurrentLanguageOutput("Farmer's Sell Price")}</div>
            <div>{getCurrentLanguageOutput("Consumer's Buy Price")}</div>
          </div>
          <div className="flex flex-col items-center">
            <div>
              <span className="text-sm bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded">
                {getCurrentLanguageOutput("Profit")}
              </span>
            </div>
          </div>
        </div>
        <div className="flex shadow-lg flex-row items-center justify-between p-3">
          <div className="font-bold text-2xl">{getCurrentLanguageOutput("Potato")}</div>
          <div className="flex flex-col font-thin text-sm items-center">
            <div className="tracking-tighter">40.00</div>
            <div>95.00</div>
          </div>
          <div className="flex flex-col items-center">
            <div>
              <span className="text-sm bg-red-500 hover:bg-red-700 text-white font-bold py-1 px-4 rounded">
                -45000
              </span>
            </div>
          </div>
        </div>
        <div className="flex shadow-lg flex-row items-center justify-between p-3">
          <div className="font-bold text-2xl">{getCurrentLanguageOutput("Tomato")}</div>
          <div className="flex flex-col font-thin text-sm items-center">
            <div className="tracking-tighter">60.00</div>
            <div>105.00</div>
          </div>
          <div className="flex flex-col items-center">
            <div>
              <span className="text-sm bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded">
                40000
              </span>
            </div>
          </div>
        </div>
        <div className="flex shadow-lg flex-row items-center justify-between p-3">
          <div className="font-bold text-2xl">{getCurrentLanguageOutput("Onion")}</div>
          <div className="flex flex-col font-thin text-sm items-center">
            <div className="tracking-tighter">27.00</div>
            <div>52.00</div>
          </div>
          <div className="flex flex-col items-center">
            <div>
              <span className="text-sm bg-blue-500 hover:bg-blue-700 text-white font-bold py-1 px-4 rounded">
                50000
              </span>
            </div>
          </div>
        </div>
        <div className="justify-center flex items-center mt-5">
          <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
                  onClick={() => {
                    setOpen("visible")
                  }}
          >
            + Add Stock
          </button>
        </div>
      </div>
      <div className={open}>
        <PopupView children={[<AddStock setOpen={setOpen}/>]}/>
      </div>
    </div>
  );
};
