import {Home} from "./pages/home";
import React, {useEffect, useState} from "react";
import {DEFAULT_LANG_CODE} from "./config/languageConfig";
import Navbar from "./pages/components/menu_bar";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import {InvestmentForm} from "./pages/applyForInvestment/investmentForm";
import {MarketTrends} from "./pages/marketTrends";
import {ExpertFeedback} from "./pages/expertFeedback";
import {ApplyForInvestment, FertilizerShop} from "./pages/applyForInvestment";
import {InvestmentDetailView} from "./pages/applyForInvestment/investmentDetailView";
import {WeatherForecast} from "./pages/weatherForecast";
import {DiseaseDetection} from "./pages/diseaseDetection";
import {Login} from "./pages/user/login";
import {StockManagement} from "./pages/stockManagement/stockManagement";
import { WorkforceManagement } from "./pages/workforceManagement";

function App() {
  const [appLang, setAppLang] = useState(DEFAULT_LANG_CODE);
  useEffect(() => {
    const lang = localStorage.getItem("lang");
    if (lang) {
      setAppLang(lang);
    }
  }, []);

  return (
    <div>
      <BrowserRouter>
        <Navbar langData={{appLang: appLang, setAppLang: setAppLang}}/>

        <Routes>
          <Route path="/" element={<Home/>}/>
          <Route path="/invest-in-farms" element={<InvestmentForm/>}/>
          <Route path="/market-trends" element={<MarketTrends/>}/>
          <Route path="/expert-feedback" element={<ExpertFeedback/>}/>
          <Route path="/invest-in-farm" element={<ApplyForInvestment/>}/>
          <Route path="/farm-detail">
            <Route path=":farmCode" element={<InvestmentDetailView/>}/>
          </Route>
          <Route path={"/investment-form"}>
            <Route path=":farmCode" element={<InvestmentForm/>}/>
          </Route>
          <Route path="/weather-forecast" element={<WeatherForecast/>}/>
          <Route path="/disease-detection" element={<DiseaseDetection/>}/>
          <Route path="/login" element={<Login/>}/>
          <Route path="/add-to-stock" element={<StockManagement/>}/>
          <Route path="/fertilizer-shop" element={<FertilizerShop/>}/>
          <Route
            path="workforce-management"
            element={<WorkforceManagement />}
          />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
