import React from "react";
import { getCurrentLanguageOutput } from "../../config/languageConfig";
import { Link } from "react-router-dom";

export default function Navbar({ fixed, additionalHeader = "", langData }) {
  const [navbarOpen, setNavbarOpen] = React.useState(false);

  return (
    <nav className=" z-10 flex flex-wrap items-center justify-between px-2 py-3 bg-green-500 sticky top-0">
      <div className="container px-4 mx-auto flex flex-wrap items-center justify-between">
        <div className="w-full relative flex justify-between lg:w-auto lg:static lg:block lg:justify-start">
          <Link to={"/"}>
            <span className="text-sm font-bold leading-relaxed inline-block mr-4 py-2 whitespace-nowrap uppercase text-white">
              {getCurrentLanguageOutput("KHETI")}
              {additionalHeader}
            </span>
          </Link>
          <button
            className="text-white cursor-pointer text-xl leading-none px-3 py-1 border border-solid border-transparent rounded bg-transparent block lg:hidden outline-none focus:outline-none"
            type="button"
            onClick={() => setNavbarOpen(!navbarOpen)}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          </button>
        </div>
        <div
          className={
            "lg:flex flex-grow items-center" +
            (navbarOpen ? " flex" : " hidden")
          }
          id="example-navbar-danger"
        >
          <ul className="flex flex-col lg:flex-row list-none lg:ml-auto">
            <li className="nav-item">
              <Link to="/login">
                <span className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75">
                  <span className="ml-2">
                    {getCurrentLanguageOutput("Login")}/
                    {getCurrentLanguageOutput("Sign Up")}
                  </span>
                </span>
              </Link>
            </li>
            <li className="nav-item">
              <span className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75">
                <span className="ml-2">
                  {getCurrentLanguageOutput("Settings")}
                </span>
              </span>
            </li>
            <li className="nav-item">
              <span className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75">
                <span className="ml-2">
                  {getCurrentLanguageOutput("My Farm")}
                </span>
              </span>
            </li>
            <li className="nav-item">
              <span className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75">
                <button
                  className="ml-2 font-bold"
                  onClick={() => {
                    langData.setAppLang((prevState) => {
                      const newLang = prevState === "en" ? "np" : "en";
                      localStorage.setItem("lang", newLang);
                      return newLang;
                    });
                  }}
                >
                  {langData.appLang === "en"
                    ? "नेपाली भाषा"
                    : "English Language"}
                </button>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
